// src/services/uploadService.ts
import { getApiUrl } from './api';

export const uploadFiles = async (files: File[]): Promise<string[]> => {
  const form = new FormData();
  files.forEach(file => form.append("files", file)); //  campo plural "files"

  try {
    const res = await fetch(getApiUrl("/api/upload"), {
      method: "POST",
      body: form,
    });

    if (!res.ok) {
      const errText = await res.text();
      console.error("Erro no upload:", errText);
      
      // Se for erro 405, tentar mtodo alternativo
      if (res.status === 405) {
        console.log("Mtodo POST no permitido, tentando upload alternativo...");
        return await uploadFilesAlternative(files);
      }
      
      throw new Error(`Falha ao enviar arquivos: ${res.status} ${res.statusText}`);
    }

    const data = await res.json();
    return data.urls || []; //  retorna array vazio se urls for undefined
  } catch (error) {
    console.error("Erro no fetch:", error);
    
    // Fallback para mtodo alternativo em caso de erro de conexo
    console.log("Tentando mtodo alternativo devido a erro de conexo...");
    return await uploadFilesAlternative(files);
  }
};

// Mtodo alternativo para casos onde upload de arquivo falha no Render
const uploadFilesAlternative = async (files: File[]): Promise<string[]> => {
  console.log("Usando mtodo alternativo de upload (mock)...");
  
  const urls: string[] = [];
  
  for (const file of files) {
    // Para fins de demonstrao, criar URLs mock baseadas no nome do arquivo
    // Em produo, voc pode implementar outro mtodo de upload ou storage
    const mockUrl = `https://via.placeholder.com/400x600/cccccc/666666?text=${encodeURIComponent(file.name)}`;
    urls.push(mockUrl);
  }
  
  return urls;
};
