//  COMPONENTE PDFNotes - Sistema de Anotaes Flutuantes
// 
//  OBJETIVO: Permitir que o usurio faa anotaes enquanto l PDFs
//  CONCEITO: Botes flutuantes + balo de anotao que se sobrepe ao contedo

import { useEffect, useState, useRef, memo } from "react";

//  TYPESCRIPT: Define a estrutura de uma anotao
type Note = {
  id: number;      // Identificador nico da nota
  texto: string;   // Contedo da anotao
  data: string;    // Quando foi criada
};

//  PROPS: O que este componente recebe do "pai" (ContentViewer)
interface Props {
  conteudoId: number; // ID do PDF/contedo para salvar as anotaes
}

//  API: Pega a URL da API das variveis de ambiente
const API = import.meta.env.VITE_API_URL;

//  COMPONENTE PRINCIPAL: React.FC = React Function Component
const PDFNotes: React.FC<Props> = ({ conteudoId }) => {
  //  DEBUG: Logs para acompanhar renderizaes
  console.log('PDFNotes renderizado com conteudoId:', conteudoId, 'em:', new Date().toISOString());
  
  //  ESTADO (useState): Variveis que podem mudar e fazem o componente re-renderizar
  const [notes, setNotes] = useState<Note[]>([]);           // Lista de anotaes
  const [input, setInput] = useState("");                   // Texto que o usurio est digitando
  const [showBalloon, setShowBalloon] = useState(false);    // Se o balo est aberto/fechado
  
  //  REF: Referncia para um elemento HTML (para scroll automtico)
  const scrollRef = useRef<HTMLDivElement>(null);

  console.log('PDFNotes state - showBalloon:', showBalloon, 'notes:', notes.length);

  //  FUNO DE IMPRESSO: Cria uma pgina HTML para imprimir as anotaes
  // CONCEITO: window.open() abre uma nova janela/aba
  const handlePrintNotes = () => {
    const win = window.open('', '_blank'); // Abre nova aba em branco
    if (!win) return; // Se foi bloqueado pelo browser, para aqui
    
    //  HTML: Monta uma pgina completa para impresso
    const html = `
      <html>
        <head>
          <title>Relatrio de Anotaes</title>
          <style>
            /*  CSS para deixar a impresso bonita */
            body { font-family: Arial, sans-serif; margin: 40px; }
            h1 { text-align: center; margin-bottom: 32px; }
            .note { border-bottom: 1px solid #ccc; padding: 16px 0; }
            .note:last-child { border-bottom: none; }
            .note-text { font-size: 1.1em; margin-bottom: 8px; }
            .note-date { font-size: 0.9em; color: #666; text-align: right; }
          </style>
        </head>
        <body>
          <h1>Relatrio de Anotaes</h1>
          <div>
            ${notes.map(n => `
              <div class="note">
                <div class="note-text">${n.texto.replace(/</g, '&lt;').replace(/>/g, '&gt;')}</div>
                <div class="note-date">${new Date(n.data).toLocaleString('pt-BR')}</div>
              </div>
            `).join('')}
          </div>
        </body>
      </html>
    `;
    
    //  Escreve o HTML na nova janela e imprime
    win.document.write(html);
    win.document.close();
    win.focus();
    setTimeout(() => win.print(), 500); // Aguarda 500ms e abre dilogo de impresso
  };

  //  useEffect #1: CARREGA anotaes existentes do servidor
  // CONCEITO: Executa quando o componente monta ou conteudoId muda
  useEffect(() => {
    if (!conteudoId) return; // Se no tem ID, no faz nada

    //  FETCH: Busca dados da API (como um WhatsApp pedindo mensagens)
    fetch(`${API}/api/anotacoes/${conteudoId}`)
      .then((res) => res.json())           // Converte resposta para JSON
      .then((data) => {
        //  Verifica se recebeu uma lista
        if (Array.isArray(data)) {
          setNotes(
            data.map((a, i) => ({
              id: a.id ?? Date.now() + i,
              texto: a.texto,
              data: new Date().toISOString(),
            }))
          );
        }
      })
      .catch(console.error);
  }, [conteudoId]);

  useEffect(() => {
    if (!conteudoId || notes.length === 0) return;

    fetch(`${API}/api/anotacoes/${conteudoId}`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(notes.map((n) => n.texto)),
    }).catch(console.error);
  }, [notes, conteudoId]);


  return (
    <div 
      style={{
        position: 'fixed',
        bottom: '20px',
        right: '20px',
        zIndex: 999999,
        fontFamily: 'Arial, sans-serif'
      }}
    >
      {/* Boto de imprimir anotaes */}
      <button
        onClick={handlePrintNotes}
        style={{
          position: 'absolute',
          bottom: '70px',
          right: '0px',
        }}
      >
        
      </button>
      {/* Boto principal COM CSS INLINE */}
      <button
        onClick={() => {
          console.log('Boto principal clicado em:', new Date());
          setShowBalloon(!showBalloon);
        }}
        style={{
          position: 'absolute',
          bottom: '70px',
          right: '0px',
          backgroundColor: '#3b82f6',
          color: 'white',
          padding: '12px',
          borderRadius: '50%',
          border: 'none',
          boxShadow: '0 2px 8px rgba(0,0,0,0.2)',
          cursor: 'pointer',
          zIndex: 999999
        }}
      >
        
      </button>
      
      {/* Balo COM CSS INLINE */}
      {showBalloon && (
        <div 
          style={{
            position: 'absolute',
            bottom: '130px',
            right: '0px',
            backgroundColor: 'white',
            border: '2px solid #ccc',
            boxShadow: '0 4px 16px rgba(0,0,0,0.2)',
            borderRadius: '8px',
            padding: '16px',
            width: '320px',
            zIndex: 999998
          }}
        >
          <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: '12px' }}>
            <h3 style={{ fontWeight: 'bold', margin: 0 }}>Anotaes</h3>
            <button
              onClick={() => setShowBalloon(false)}
              style={{
                color: '#666',
                border: 'none',
                backgroundColor: 'transparent',
                fontSize: '20px',
                cursor: 'pointer'
              }}
            >
              
            </button>
          </div>
          
          <textarea
            value={input}
            onChange={(e) => setInput(e.target.value)}
            rows={3}
            placeholder="Sua anotao..."
            style={{
              width: '100%',
              padding: '8px',
              border: '1px solid #ccc',
              borderRadius: '4px',
              marginBottom: '12px',
              fontFamily: 'Arial, sans-serif',
              fontSize: '14px'
            }}
          />
          
          <button
            onClick={() => {
              if (input.trim()) {
                const nova = {
                  id: Date.now(),
                  texto: input,
                  data: new Date().toISOString(),
                };
                setNotes([...notes, nova]);
                setInput("");
                alert('Anotao salva!');
              }
            }}
            disabled={!input.trim()}
            style={{
              width: '100%',
              backgroundColor: input.trim() ? '#3b82f6' : '#ccc',
              color: 'white',
              padding: '8px',
              borderRadius: '4px',
              border: 'none',
              cursor: input.trim() ? 'pointer' : 'not-allowed'
            }}
          >
            Salvar
          </button>
            {/* Boto de imprimir anotaes */}
            <button
            onClick={handlePrintNotes}
            style={{
              width: '100%',
              backgroundColor: '#f59e42',
              color: 'white',
              padding: '8px',
              borderRadius: '4px',
              border: 'none',
              cursor: 'pointer',
              marginTop: '8px'
            }}
            >
             Imprimir anotaes
            </button>
          {notes.length > 0 && (
            <div style={{ marginTop: '12px', maxHeight: '160px', overflowY: 'auto' }}>
              <h4 style={{ fontSize: '14px', fontWeight: 'medium', marginBottom: '8px' }}>
                Anotaes ({notes.length}):
              </h4>
              {notes.map((note) => (
                <div 
                  key={note.id} 
                  style={{
                    backgroundColor: '#fef3c7',
                    padding: '8px',
                    borderRadius: '4px',
                    marginBottom: '8px',
                    fontSize: '12px'
                  }}
                >
                  {note.texto}
                </div>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  );
};

export default memo(PDFNotes);
